<template>
  <div class="partner-home-wrap">
    <div v-title>噢啦渠道商</div>
    <div class="partner-home">

      <!--通知栏   一期暂时隐藏-->
      <div class="callboard" v-if="false">
        <img src="../../assects/images/order_notice.svg" class="notice-icon">
        <ul class="info-list">
          <li class="info-item">【卢瑶瑶】捐赠的iPhone X于16:00送达，该订单…</li>
        </ul>
      </div>

      <!--渠道合作商简单信息栏-->
      <div class="partner-info">
        <!--消息通知 一期暂时隐藏-->
        <div class="news-notice" v-if="false">
          <img src="../../assects/images/notice.svg">
          <p class="news-num">3</p>
        </div>
        <div class="info-head-wrap">
          <img :src="channelOfficePartnerImg" alt="" class="info-head">
        </div>
        <p class="info-name">{{ channelOfficePartnerName }}</p>
        <p class="info-id">ID：{{ channelOfficePartnerId }}
          <router-link
            :to="{
            path: '/partner-account'
          }">
            <img src="../../assects/images/channel_order_information.png" class="go-info-icon">
          </router-link>
        </p>
      </div>

      <!--订单基本统计结果-->
      <div class="order-rude">
        <div class="rude-head">
          <img src="../../assects/images/site.svg" alt="" class="head-icon">
          <span>正在运作服务点</span>
          <router-link
            :to="{
            path: '/service-list'
          }">
            <img src="../../assects/images/choose_more.svg" class="service-detail">
          </router-link>
          <span class="service-num">{{ channelCount }}</span>
        </div>
        <div class="rude-num">
          <div class="num-left">
            <img src="../../assects/images/order_total.svg">
            <div class="order-num">
              <p>{{ recycleCount }}</p>
              <h3>总订单数</h3>
            </div>
          </div>
          <div class="num-right">
            <img src="../../assects/images/channel_order_effective.svg">
            <div class="order-num">
              <p>{{ todayRecycleCount }}</p>
              <h3>今日有效订单</h3>
            </div>
          </div>
        </div>
      </div>

      <!--可视化数据图-->
      <div class="order-chart">
        <ul class="tab-list">
          <li class="tab-item"
              :class="{tabActive: activeIndex === 'D'}"
              @click="emitTab('D')">日
          </li>
          <li class="tab-item"
              :class="{tabActive: activeIndex === 'W'}"
              @click="emitTab('W')">周
          </li>
          <li class="tab-item"
              :class="{tabActive: activeIndex === 'M'}"
              @click="emitTab('M')">月
          </li>
        </ul>
        <chart :options="option"></chart>
        <p class="point-detail"> {{ echartTitle }}</p>
        <p class="hint">左右滑动查看更多、点击空点查看</p>
      </div>
    </div>
  </div>
</template>
<script>
  import {mapState} from 'vuex'
  import {Toast} from 'mint-ui'
  import {partnerInitApi, getChartsApi} from '@/API'
  import {
    getNowFormatDate,
    getBackFormatDate
  } from '@/support'

  export default {
    data() {
      let self = this
      return {
        activeIndex: 'D',
        echartTitle: '',
        recycleCount: '', //	int	总订单数
        todayRecycleCount: '', //	int	今日订单数
        channelCount: '', //	int	服务站数量
        channelOfficePartnerName: '', //	string	合作商名称
        channelOfficePartnerId: '', //	int	合作商ID
        channelOfficePartnerImg: '', //	string	合作商图标
        chartsList: {},  // 日 周 月 的折线图数据与加载记录
        option: {
          animationDuration: 1000,
          dataZoom: [
            {
              type: 'inside',
              start: 60,
              end: 100
            }
          ],
          xAxis: {
            data: ['2/1', '2/2', '2/3', '2/4', '2/5', '2/6', '2/7', '2/8', '2/9', '2/1', '2/2', '2/3', '2/4', '2/5', '2/6', '2/7', '2/8', '2/9']
          },
          yAxis: {
            splitLine: {
              show: false
            }
          },
          tooltip: {
            trigger: 'axis',
            formatter: function (params, ticket, callback) {
              self.echartTitle = params[0].axisValue + '订单数为' + params[0].value
              callback()
            }
          },
          series: [{
            name: '订单数',
            type: 'line',
            symbolSize: 10,
            areaStyle: {
              color: 'rgba(54, 173, 0, 0.1)'
            },
            itemStyle: {
              normal: {
                color: '#00EE00', // 拐点颜色
                label: { // 标签文字
                  show: true
                },
                lineStyle: { // 折线样式
                  color: '#00EE00',
                  width: 2
                }
              }
            },
            data: [5, 20, 36, 18, 10, 25, 10, 13, 25, 5, 20, 36, 40, 7, 30, 20, 18, 13]
          }]
        }
      }
    },
    computed: {
      ...mapState('partnerHome', [
        'a'
      ])
    },
    created() {
      let self = this
      partnerInitApi({
        beginTime: getNowFormatDate(),
        endTime: getBackFormatDate(1),
        dayType: 1
      })
        .then(res => {
          if (res.status === 'true') {
            self.recycleCount = res.info.count.recycleCount
            self.todayRecycleCount = res.info.count.todayRecycleCount
            self.channelCount = res.info.count.channelCount
            self.channelOfficePartnerName = res.info.channelOfficePartnerName
            self.channelOfficePartnerId = res.info.channelOfficePartnerId
            self.channelOfficePartnerImg = res.info.channelOfficePartnerImg
            self.chartsList.D = res.info.recycleOrderCount
            self.chartsList.D.Y = self.option.series.data = res.info.recycleOrderCount.map(item => item.time)
            self.chartsList.D.X = self.option.xAxis.data = res.info.recycleOrderCount.map(item => item.recycleCount)
            self.option.xAxis.data[self.option.xAxis.data.length - 1] = '今日'
            self.echartTitle = '今日订单数' + self.option.series.data[self.option.series.data.length - 1]
          }
        })
        .catch()
    },
    methods: {
      // 切换tab
      emitTab: function (type) {
        let self = this
        if (self.activeIndex === type) {
          return
        }
        // self.$store.commit('partnerHome/setChartsList')
        console.log(self.a)
        let now = ''
        switch (type) {
          case 'D':
            now = '今日'
            break
          case 'W':
            now = '本周'
            break
          case 'M':
            now = '本月'
            break
          default:
            now = ''
        }
        // 如果该图表的数据已经加载过
        if (self.chartsList[type]) {
          self.activeIndex = type
          self.option.series.data = self.chartsList[type].Y
          self.option.xAxis.data = self.chartsList[type].X
          self.echartTitle = now + '订单数' + self.option.series.data[self.option.series.data.length - 1]
          return
        }
        getChartsApi({
          type: type
        })
          .then(res => {
            if (res.status === 'true') {
              self.activeIndex = type
              self.chartsList[type] = res.info.recycleOrderCount
              self.chartsList[type].Y = self.option.series.data = res.info.recycleOrderCount.map(item => item.time)
              self.chartsList[type].X = self.option.xAxis.data = res.info.recycleOrderCount.map(item => item.recycleCount)
              self.option.xAxis.data[self.option.xAxis.data.length - 1] = now
              self.echartTitle = now + '订单数' + self.option.series.data[self.option.series.data.length - 1]
            } else {
              Toast('数据加载失败')
            }
          })
          .catch(() => {
            Toast('数据加载失败')
          })
      },
    }
  }
</script>
<style lang="less" scoped>
  .partner-home {
    /*头部通知栏*/
    .callboard {
      height: 28px;
      overflow: hidden;
      background: #FFF3DC;
      .notice-icon {
        float: left;
        margin-top: 7px;
        padding: 0 5px 0 5px;
        height: 14px;
      }
      .info-list {
        .info-item {
          font-size: 14px;
          color: #E17100;
          line-height: 28px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }
    }
    /*渠道商简介*/
    .partner-info {
      position: relative;
      padding: 25px 0 25px 0;
      text-align: center;
      background: url("../../assects/images/info-background.jpg") no-repeat;
      background-size: 100% 100%;
      .news-notice {
        display: inline-block;
        position: absolute;
        right: 30px;
        top: 10px;
        img {
          padding-top: 10px;
          width: 25px;
          height: 38px;
        }
        .news-num {
          position: absolute;
          right: -40%;
          top: 0;
          min-width: 19px;
          height: 19px;
          padding: 2px;
          text-align: center;
          font-size: 11px;
          color: #FFFFFF;
          line-height: 14px;
          background: #FF4848;
          border-radius: 50%;
        }
      }
      .info-head-wrap {
        display: inline-block;
        width: 50px;
        height: 50px;
        box-shadow: 0 0 3px 3px white;
        border-radius: 50%;
        overflow: hidden;
      }
      .info-head {
        display: inline-block;
        width: 50px;
        height: 50px;
        box-shadow: 0 0 3px 3px white;
        border-radius: 50%;
      }
      .info-name {
        font-size: 16px;
        color: #FFFFFF;
        line-height: 2;
      }
      .info-id {
        font-size: 13px;
        color: #FFFFFF;
        line-height: 2;
        .go-info-icon {
          height: 13px;
          margin-top: -3px;
        }
      }
    }
    /*总体数据*/
    .order-rude {
      width: 90vw;
      min-height: 20vh;
      margin: -22px auto 0 auto;
      position: relative;
      z-index: 2; // 置于简介浪上面
      background: #FFFFFF;
      box-shadow: 0 0 20px 0 rgba(18, 21, 58, 0.18);
      border-radius: 12px;
      .rude-head {
        font-family: PingFangSC-Regular;
        padding-left: 15px;
        margin-top: 15px;
        font-size: 16px;
        color: #191D21;
        line-height: 3;
        border-bottom: 1px solid #E9ECF5;;
        .head-icon {
          height: 16px;
        }
        .service-num {
          float: right;
          margin: 16px 0 0 0;
          display: inline-block;
          min-width: 20px;
          height: 20px;
          text-align: center;
          border-radius: 50%;
          background: #FFD77D;
          border: 1px solid #FFB717;
          font-size: 14px;
          color: #FFFFFF;
          line-height: 20px;
        }
        .service-detail {
          height: 18px;
          float: right;
          margin: 16px 15px 0 15px;
        }
      }
      .rude-num {
        height: 14vh;
        display: flex;
        flex-direction: row;
        align-items: center;
        .num-left {
          width: 50%;
          display: flex;
          flex-direction: row;
          justify-content: center;
          align-items: center;
          border-right: 1px solid #E9ECF5;
          img {
            width: 30px;
            margin-right: 10px;
          }
          .order-num {
            p {
              font-size: 22px;
              color: #191D21;
              line-height: 22px;
            }
            h3 {
              font-size: 13px;
              color: #9D9DA5;
              line-height: 13px;
            }
          }
        }
        .num-right {
          width: 50%;
          display: flex;
          flex-direction: row;
          justify-content: center;
          align-items: center;
          img {
            width: 30px;
            margin-right: 10px;
          }
          .order-num {
            p {
              font-size: 22px;
              color: #191D21;
              line-height: 22px;
            }
            h3 {
              font-size: 13px;
              color: #9D9DA5;
              line-height: 13px;
            }
          }
        }
      }
    }
    /*折线图*/
    .order-chart {
      width: 90vw;
      min-height: 40vh;
      margin: 20px auto;
      background: #FFFFFF;
      box-shadow: 0 0 20px 0 rgba(18, 21, 58, 0.18);
      border-radius: 12px;
      .tab-list {
        display: flex;
        flex-direction: row;
        padding: 0 20px;
        .tab-item {
          flex: 1;
          text-align: center;
          font-size: 16px;
          color: #191D21;
          line-height: 3;
          margin: 0 20px;
        }
        .tabActive {
          color: #FFB717;
          border-bottom: 4px solid #FFB717;
        }
      }
      .echarts {
        display: block;
        width: 90%;
        margin: -20px auto 0 auto;
        height: 40vh;
      }
      .point-detail {
        margin-top: -20px;
        text-align: center;
        font-size: 14px;
        color: #0D000F;
        line-height: 1;
      }
      .hint {
        text-align: center;
        color: #FFB717;
        font-size: 12px;
        line-height: 2;
      }
    }
  }
</style>



